import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LightHttp, LightUser, LightJsonUtil } from './../../../light/light-out/light-out.module';

@IonicPage()
@Component({
    selector: 'light-writer-page',
    templateUrl: 'light-writer-page.html'
})
export class LightWriterPage {

    //表单数据
    writeData: any;

    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public lightHttp: LightHttp,
        public lightUser: LightUser

    ) {

    }

    ionViewDidLoad() {
        //初始化业务数据
        this._initData();
    }

    /**
    * 通过经纬度设置当前的地名地址
    * 
    * @private
    * @param {number} lat 
    * @param {number} lng 
    * @memberof PointAddModalPage
    */
    private _initData(): void {
        this.lightHttp.post("DelegateTaskNote/GetTaskNoteWriterForm", {
            userName: "超级管理员",
            code: '104EEBBF98994C8BBAD0162106F3671D'
        }).then((data) => {
            this.writeData = LightJsonUtil.toObject(data);
        }).catch(err => console.log(err));
    }


    /**
     * 点击确定按钮，提交数据
     * 
     * @memberof TransferboxAddModalPage
     */
    public submit(): void {
        console.log(this);
    }


}



